.formatting-box {
  text-align: left;
  padding: 30px 0;
  margin-left: 8px;
  margin-top: 20px;
}
.DraftEditor-editorContainer {
  width: 870px;
  height: 300px;
  margin-top: 10px;
}
.public-DraftEditor-content {
  max-height: 300px;
  overflow: auto;
}

@media only screen and (max-width: 650px) {
  .toolbar {
    @include flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
  .row2 {
    flex: 1 0 100%;
    margin: 1% 0 0 14%;
  }
}

.header-options {
  padding: 5px;
}
