.form-container {
    width: 400px;
    min-height: 435px;
    height: auto;
    border-radius: 5px;
    box-shadow: 0 9px 50px hsla(20, 67%, 75%, 0.31);
    padding: 0 2%;
    background-image: linear-gradient(-225deg, $grey 50%, $green 50%);
    @include flex;
    -webkit-justify-content: center;
    justify-content: center;
    align-items: center;
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
}
@media (max-width: 480px) {
    .form-container {
        width: 320px;
    }
}

.form-header {
    font-size: 200%;
    margin: 0% auto 10% auto;
    text-align: center;
    font-family: $playfair-display;
    color: $white;
}
// autocomplete style
.form-input:-webkit-autofill,
.form-input:-webkit-autofill:hover,
.form-input:-webkit-autofill:focus,
.form-input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px $white inset !important;
    box-shadow: 0 0 0 30px $white inset !important;
}

/* Show/hide password icon */
.eye {
    background: $white;
    color: $charcoal;
    margin-left: -20px;
    padding: 15px 8px 32px 6px;
    border-radius: 0px 5px 5px 0px;
    float: right;
    position: relative;
    right: 10%;
    bottom: -14px;
    z-index: 5;
    cursor: pointer;
}

.form-input {
    height: 50px;
    margin-top: 4%;
    padding: 15px;
    font-size: 16px;
    width: 240px;
    font-family: $abel;
    color: $charcoal;
    outline: none;
    border: none;
    border-radius: 0px 5px 5px 0px;
    transition: 0.2s linear;
}
.form-input:focus {
    -webkit-transform: translateX(-2px);
    -ms-transform: translateX(-2px);
    transform: translateX(-2px);
}

.form-group {
    margin-left: 12px;
}

// buttons
.submit-auth {
    display: inline-block;
    color: $charcoal;
    width: 270px;
    height: 50px;
    padding: 0 0px;
    background: $white;
    border-radius: 5px;
    outline: none;
    border: none;
    cursor: pointer;
    text-align: center;
    transition: all 0.2s linear;
    margin: 15% 12px;
    letter-spacing: 0.05em;
    &:hover {
        @include transform;
        box-shadow: none;
    }
}

/*     Sign Up button  */
.sign-up {
    display: inline-block;
    margin-left: 2%;
    background: #b8f2e6;
    &:hover {
        -webkit-animation: ani9 0.4s ease-in-out infinite alternate;
        animation: ani9 0.4s ease-in-out infinite alternate;
    }
}

.input-icon {
    background: $white;
    color: $charcoal;
    padding: 17.4px 0px 16px 10px;
    border-radius: 5px 0px 0px 5px;
}
/* buttons hover Animation */
@-webkit-keyframes ani9 {
    0% {
        @include transform;
    }
    100% {
        -webkit-transform: translateY(5px);
        -ms-transform: translateY(5px);
        transform: translateY(5px);
    }
}

@keyframes ani9 {
    0% {
        @include transform;
    }
    100% {
        -webkit-transform: translateY(5px);
        -ms-transform: translateY(5px);
        transform: translateY(5px);
    }
}
